// fetchData : public  data  json   
const fetchData = (FilE_NAME) => {
  return fetch(`${process.env.PUBLIC_URL}/data/${FilE_NAME}.json`)
    .then(response => response.json());
};

const App = () => {
  // trip:   , setTrip:   
  const [trip, setTrip] = useState([]);
  // passengers:    
  const [passengers, setPassengers] = useState([]);

  // isloaded:    , setIsLoaded:   
  const [isloaded, setIsLoaded] = useState(false);
  
  // getData : trips     
  const getData = useCallback(async () => {
    const TRIP = await fetchData("trips");
    let PASSENGERS = [];
    try {
      PASSENGERS = await fetchData("passengers");
    } catch (e) {
      PASSENGERS = [];
    }

    setTrip(() => TRIP);
    setPassengers(() => PASSENGERS || []);

    setIsLoaded(true);
  }, []);

  //    getData   
  useEffect(() => {
    getData();
  }, [getData]);

  return (
    <div className="container">
      {/*    Splash   */}
      {!isloaded && <Splash />}
      {/*     Trip  trip   */}
      {isloaded && (
        <Trip trip={trip} passengers={passengers} />
      )}
    </div>
  );
};

export default App;
